}

.detailsList li {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-left: 1ch;
  transition: all 0.4s ease;
}
.detailsList li::marker {
  content: "\2022";
  content: "➤";
  color: orange;
  font-size: 20px;
}

.detailsList li h3 {
