}
.detailsList li:last-child {
  font-size: 1.15em;
  letter-spacing: 0;
  font-weight: bolder;
}

.youtubeButton {
  text-decoration: none;
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
