/* KEYFRAMES */

@-webkit-keyframes animateBubble {
  0% {
    margin-top: 30%;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    /* transform: scale(0.5); */
    opacity: 0.2;
  }
  100% {
    margin-top: -40%;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes animateBubble2 {
  0% {
    margin-top: 40%;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    /* transform: scale(0.5); */
  }
  100% {
    margin-top: -10%;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes animateBubble2 {
  0% {
    margin-top: 40%;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    /* transform: scale(0.5); */
  }
  100% {
    margin-top: -10%;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes animateBubble {
  0% {
    margin-top: 30%;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    /* transform: scale(0.5); */
    opacity: 0.2;
  }
  100% {
    margin-top: -40%;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes sideWays {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 50px;
  }
}

@keyframes sideWays {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 50px;
  }
}
@-webkit-keyframes sideWays2 {
  0% {
    margin-left: -20px;
  }
  100% {
    margin-left: 45px;
  }
}
@keyframes sideWays2 {
  0% {
    margin-left: -20px;
  }
  100% {
    margin-left: 45px;
  }
}

