/** Code for hover info **/

.skillsContainer .iconWrapper {
  height: 6vw;
  width: 6vw;
  border-radius: 50%;
  background: rgb(136, 135, 135);
  background: #202020cb;
  transition: ease all 0.5s;
  position: relative;

  border: 0 solid;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
  outline: 1px solid;
  outline-offset: 0px;
  text-shadow: none;
  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
}

.skillsContainer .iconWrapper:hover {
  border: 1px solid;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.2),
    0 0 20px rgba(255, 255, 255, 0.5);
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.skillsContainer .iconWrapper::after {
  content: attr(data-hover);
  position: absolute;
  top: 50%;
  left: 20%;
  opacity: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.5em;
  padding: 0.5em 0.8em;

  display: flex;
  align-content: center;
  text-align: center;
  justify-content: center;
  margin-left: -20%;
  background: var(--orangeLight);
  color: #fff;
  pointer-events: none; /* This prevents the box from apearing when hovered. */
  transition: opacity 250ms, top 250ms;
  z-index: 999;
}
.skillsContainer .iconWrapper:hover {
  z-index: 992;
} /* Keeps the info boxes on top of other elements */
.skillsContainer .iconWrapper:hover::after,
.skillsContainer .iconWrapper:hover::before {
  opacity: 1;
}
.skillsContainer .iconWrapper:hover::after {
  top: 110%;
}
.skillsContainer .iconWrapper:hover::before {
  top: 95%;
}

